<template>
    <div class="out">
        <div class="head">
            <img @click="goback" src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADoAAAA6CAMAAADWZboaAAAAt1BMVEVHcEz
            ///////////////////////////////////////////////////////////////////////////////////
            " alt="">
            <p>剧照({{film.length}})</p>
            <i></i>
        </div>
       <div v-for="(item,index) in film" :key="index" class="juzhao">
           <img :src="item" alt="">
       </div>
    </div>
</template>

<script>
import {detailListData} from '@/api/api'
export default {
    data() {
        return {
            film:{}
        }
    },
    async mounted() {
        let ret = await detailListData(this.$route.params.filmId)
        this.film = ret.data.data.film.photos
    },
    methods:{
        goback:function(){
            this.$router.go(-1)
        }
    }
}
</script>

<style lang="scss" scoped>
    .out{
        display: flex;
        flex-wrap: wrap;
        img{
            width: 136px;
            height: 140px;
            margin-right: 2px;
        }
        .head{
            width: 100%;
            height: 45px;
            display: flex;
            padding:0 5px;
            justify-content: space-between;
            align-items: center;
            img{
                width: 30px;
                height: 30px;
            }
            p{
                margin-left: -30px;
            }
        }
    }
</style>